











import Component from "vue-class-component";
import Vue from "vue";

@Component({
    props: {
        name: String
    }
})
export default class FileName extends Vue {
    public truncate: boolean = true;

    public constructor() {
        super();
    }

    public over() {
        this.truncate = false;
    }

    public leave() {
        this.truncate = true;
    }
}



