






import Component from "vue-class-component";
import Vue from "vue";

@Component({
    props: {
        title: String,
        to: String
    }
})
export default class RedirectButton extends Vue {
    public constructor() {
        super();
    }

    public redirect() {
        this.$router.push(this.$props.to);
    }

    public get destination() {
        return this.$props.to == "/" ? "Domov" : this.$props.title;
    }
}



